:root {
  --font-inter: "Inter", sans-serif;
}

body {
  font-family: var(--font-inter);
}

html {
  font-size: 100%;
  box-sizing: border-box;
  background-color: var(--bg-color);
}

body,
ul {
  margin: 0;
  padding: 0;
  color: var(--white);
}

ul {
  list-style: none;
}

a {
  color: var(--white);
}

h1,
h2,
h3,
h4,
h5,
p {
  margin-top: 0;
  margin-bottom: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

@media (max-width: 63.9375em) {
  .review {
    margin: 9.5625em 1.875em 0;
  }
}
@media (max-width: 63.9375em) {
  .review__title {
    font-size: 2.5rem;
    text-align: center;
    color: var(--main-color);
    margin-bottom: 0.3125em;
  }
}
@media (max-width: 63.9375em) {
  .review__description {
    font-size: 1.25rem;
    text-align: center;
    margin-bottom: 0.9375em;
  }
  .review__description a {
    color: var(--hyperlink-color);
  }
}
@media (max-width: 63.9375em) {
  .review__template {
    text-align: center;
    margin-bottom: 1.5625em;
  }
}
@media (max-width: 63.9375em) {
  .review__card {
    height: 17.5em;
  }
}
@media (max-width: 63.9375em) {
  .review__text {
    font-size: 2rem;
    padding: 0.9375em;
  }
}
@media (max-width: 63.9375em) {
  .review__sample {
    margin-bottom: 0.625em;
    background-color: var(--main-color);
    width: 50px;
  }
}
@media (max-width: 63.9375em) {
  .review__sample__text {
    text-align: center;
  }
}

@media (max-width: 63.9375em) {
  .footer {
    background-color: var(--footer-color);
    height: 12.5em;
  }
}
@media (max-width: 63.9375em) {
  .footer__container {
    margin: 0em 1.875em;
  }
}
@media (max-width: 63.9375em) {
  .footer__col {
    display: grid;
  }
}
@media (max-width: 63.9375em) {
  .footer__title {
    color: var(--main-color);
    font-size: 0.875rem;
    margin-bottom: 0.625em;
  }
}
.home {
  margin-bottom: 25em;
}
@media (max-width: 63.9375em) {
  .home__container {
    position: relative;
  }
}
@media (max-width: 63.9375em) {
  .home__svg__mobile {
    position: absolute;
    width: 100%;
    height: auto;
    margin-top: 15.625em;
    overflow: visible;
  }
}
@media (min-width: 40em) {
  .home__svg__mobile {
    display: none;
  }
}
@media (max-width: 63.9375em) {
  .home__svg__desktop {
    position: absolute;
    width: 100%;
    height: auto;
    margin-top: 15.625em;
    overflow: visible;
    display: none;
  }
}
@media (max-width: 63.9375em) {
  .home__text {
    margin: 0em 1.875em;
    text-align: center;
  }
}
@media (max-width: 63.9375em) {
  .home__title {
    position: relative;
    font-size: 4rem;
    color: var(--white);
    margin-bottom: 1.6875em;
    margin-top: 1.25em;
  }
}
@media (max-width: 63.9375em) {
  .home__description {
    position: relative;
    font-size: 1.25rem;
    color: var(--white);
  }
  .home__description a {
    color: var(--hyperlink-color);
  }
}

@media (max-width: 63.9375em) {
  .nav__menu {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: flex-end;
    margin-top: 3.125em;
    margin-right: 2.1875em;
  }
}
@media (max-width: 63.9375em) {
  .nav span {
    display: block;
    width: 25px;
    height: 4px;
    background-color: var(--white);
  }
  .nav span:not(:last-child) {
    margin-bottom: 5px;
  }
}
@media (min-width: 40em) {
  .nav__links {
    margin: 0 19.25em 0 19.125em;
    display: flex;
    padding: 0;
    list-style: none;
    justify-content: center;
    align-items: baseline;
  }
}
@media (max-width: 63.9375em) {
  .nav__links {
    display: none;
    flex-direction: column;
    list-style: none;
    justify-content: center;
    align-items: center;
  }
}
@media (max-width: 63.9375em) {
  .nav__logo {
    display: none;
  }
}
@media (min-width: 40em) {
  .nav__items {
    padding-right: 12.5em;
  }
}
@media (max-width: 63.9375em) {
  .nav__items {
    padding: 3.125em 0;
  }
}
.nav__text {
  color: var(--white);
  font-size: 1.5rem;
}

